<template>
  <div
    class="base-select"
    v-click-outside="closeDropdown"
    :class="{ 'base-select-border': borderOnly }"
    ref="select"
  >
    <div
      class="base-select-selected"
      :class="[
        { open, disabled },
        {
          'base-select-selected-border': borderOnly,
          'base-select-selected-active': open || modelValue,
        },
      ]"
      @click="openDropdown"
    >
      <span v-if="!selected && placeholder">
        {{ placeholder }}
      </span>
      <span class="selected ellipse" v-else>
        {{ selected && selected.value }}
      </span>
    </div>
    <div
      v-if="!disabled"
      class="base-select-items"
      :class="{ open }"
      ref="dropdown"
      :style="dropdownDirection"
    >
      <div
        v-for="(option, i) of options"
        :key="`${option.key}_${i}`"
        @click="
          selected = option;
          open = false;
          $emit('update:modelValue', option.key);
        "
      >
        {{ option.value }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    options: {
      type: Array,
      required: true,
    },
    label: {
      type: String,
      default: "",
    },
    placeholder: {
      type: [Number, String],
      default: "",
    },
    disabled: {
      type: Boolean,
    },
    borderOnly: {
      type: Boolean,
    },
    modelValue: {
      type: [String, Number],
    },
  },

  computed: {
    selected() {
      return this.modelValue
        ? this.options.find((i) => i.key == this.modelValue)
        : null;
    },
  },

  data() {
    return {
      open: false,
      dropdownDirection: null,
    };
  },

  methods: {
    openDropdown() {
      if (!this.disabled) this.open = !this.open;
      if (this.open) {
        setTimeout(() => {
          let selectTop = this.$refs.select.getBoundingClientRect().top;
          let dropdownHeight =
            this.$refs.dropdown.getBoundingClientRect().height;
          if (window.innerHeight - selectTop < dropdownHeight + 60)
            this.dropdownDirection = `top: auto; bottom: ${
              this.borderOnly ? 23 : 40
            }px`;
          else this.dropdownDirection = `top: ${this.borderOnly ? 23 : 40}px`;
        });
      }
    },

    closeDropdown() {
      this.open = false;
    },
  },
};
</script>

<style scoped lang="scss">
.base-select {
  position: relative;
  width: 100%;
  height: 40px;
  text-align: left;
  outline: none;
  &-border {
    height: 25px;
  }
  &-selected {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 16px;
    border-radius: 5px;
    border: 1px solid $grayLighter;
    transition: 0.3s ease;
    cursor: pointer;
    user-select: none;
    .selected {
      width: calc(100% - 55px);
      position: absolute;
      font-family: $regular;
      font-size: 16px;
      line-height: 19px;
      text-align: left;
    }
    > svg {
      margin-right: 8px;
    }
    &:after {
      content: "";
      width: 24px;
      height: 14px;
      position: absolute;
      top: 13px;
      right: 14px;
      background-image: url(~@/assets/icons/dropdown-arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      transition: 0.3s ease;
    }
    &.open {
      &:after {
        transform: rotate(180deg);
      }
    }
    &-active {
      background: $surfaceHover;
      border-color: $surfaceHover;
    }
    &.disabled {
      opacity: 0.5;
    }
    &-border {
      height: 25px;
      border: none;
      border-bottom: 2px solid $black;
      border-radius: 0;
      padding-left: 3px;
      transition: 0s;
      &.open {
        border: none;
        padding-bottom: 2px;
      }
      &:after {
        top: 4px;
        right: 0;
      }
    }
  }

  &-items {
    max-height: 0;
    position: absolute;
    left: 0;
    right: 0;
    background-color: $white;
    border-radius: 0px 0px 6px 6px;
    box-shadow: 0px 0px 9px rgba($black, 0.1);
    border-radius: 5px;
    padding: 8px 0;
    overflow: hidden;
    z-index: 3;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s ease;

    &.open {
      opacity: 1;
      visibility: visible;
      overflow-y: auto;
      max-height: 235px;
    }

    > div {
      min-height: 37px;
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 28px;
      padding: 0 12px;
      cursor: pointer;
      user-select: none;
      line-break: anywhere;
      transition: 0.2s ease;
      &:hover {
        background: $grayHover;
      }
    }

    &::-webkit-scrollbar {
      width: 17px;
    }

    &::-webkit-scrollbar-thumb {
      border: 7px solid transparent;
    }
  }
}
</style>
